import axios from 'axios'

const axs = axios.create({
    timeout: 30000,
    withCredentials: false
})

axs.interceptors.request.use(conf =>{
    // conf.headers['Authorization'] = localStorage.getItem("token")
    return conf
}, error => {
    console.log("请求错误", error);
    return error
})

axs.interceptors.response.use(resp =>{
    return resp
}, error => {
    console.log("服务器异常", error);
    return error
})
export default axs
