import Vue from 'vue'
// 引入路由
import VueRouter from 'vue-router'

// 使用
Vue.use(VueRouter)
// 向外导出
export default new VueRouter({
    mode: 'history',
    routes: [
        {
            path: '/',
            redirect: '/search'
        },
        {
            path: '/search',
            name: 'search',
            component: () => import('@/views/search')
        }
    ]
})